<template>
  <div class="text-field">
    <label v-if="label" class="text-field__label" :class="{'text-field__label_required': required}">
      <span>{{label}}</span>
      <app-input-wrapper :focus="focus">
        <div class="text-field__append">
          <slot name="append"></slot>
        </div>
        <input
            :type="type"
            class="text-field__input"
            :required="required"
            :placeholder="placeholder"
            :name="name"

            @focus="focus = true"
            @blur="focus = false"
        />
      </app-input-wrapper>
    </label>
  </div>
</template>

<script>
import AppInputWrapper from "@/components/base/app-input-wrapper";
export default {
  name: "app-text-filed",
  components: {AppInputWrapper},
  props: {
    label: {
      type: String,
      required: false
    },
    required: {
      type: Boolean,
      default: false
    },
    placeholder: {
      type: String,
      default: ""
    },
    type: {
      type: String,
      default: "text"
    },
    name: {
      type: String,
      default: ""
    }
  },
  data() {
    return {
      data: "",
      focus: false
    }
  },
}
</script>

<style lang="scss" scoped>
.text-field{
  margin: 5px 0;
  position: relative;
  width: 100%;
  text-align: left;
  outline: none;

  &__label {
    display: block;
    text-align: left;
    margin-top: 3px;

    &_required span::after {
      content: "*";
      color: red;

    }
  }

  &__append {
    max-width: 150px;
    background-color: #d1d1d1;
    border-radius: 5px 0 0 5px;
  }

  &__input {
    margin: 0 1em;
    flex-grow: 1;
    line-height: 34px;
    outline: none;
    border: none;

  }
}
</style>